// src/components/schedule/DepartmentColumn.tsx

import React from 'react';
import { useDroppable } from '@dnd-kit/core';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Plus, Trash2 } from 'lucide-react';
import type { Department, ShiftAssignment, Employee } from '@/types/schedule';
import { ShiftCard } from './ShiftCard';
import { format } from 'date-fns';
import { cn } from '@/lib/utils';

interface DepartmentColumnProps {
  department: Department;
  assignments: ShiftAssignment[];
  onRemoveShift: (departmentId: string, assignmentId: string) => void;
  date: Date; // Date for this column
  onAssign: (employee: Employee, departmentId: string, date: Date) => void; // Handler for D&D assignment
  isWeekView?: boolean; // Flag for potentially different rendering in week view
  onSelectEmployeeForShift: (departmentId: string, date: Date) => void; // Handler for mobile '+' button
}

export const DepartmentColumn: React.FC<DepartmentColumnProps> = ({
  department,
  assignments,
  onRemoveShift,
  date,
  onAssign, // Keep for potential future use, though mobile uses onSelectEmployeeForShift
  isWeekView = false,
  onSelectEmployeeForShift, // Receive the new handler
}) => {
  const dateKey = format(date, 'yyyy-MM-dd');
  const { setNodeRef, isOver } = useDroppable({
    id: `${department.id}_${dateKey}`, // Make ID unique per department and date
    data: {
        type: 'department',
        id: department.id,
        date: dateKey, // Pass date string in data
    }
  });

  const style = {
    // Highlight when dragging over
    backgroundColor: isOver ? 'hsl(var(--accent)/0.1)' : undefined,
    borderColor: isOver ? 'hsl(var(--accent))' : (isWeekView ? 'hsl(var(--border) / 0.3)' : undefined), // Lighter border in week view normal state
    borderWidth: '1px', // Use 1px border always
    borderStyle: isOver ? 'dashed' : 'solid',
    minHeight: isWeekView ? '40px' : '200px', // Shorter min-height for week view
    transition: 'background-color 0.2s ease, border-color 0.2s ease', // Smooth transition
    borderRadius: isWeekView ? '0.25rem' : undefined, // Adjusted rounding in week view
  };

  // Compact rendering for week view (both desktop and mobile cards use this structure internally)
  if (isWeekView) {
    return (
      <div ref={setNodeRef} style={style} className="p-0.5 space-y-0.5"> {/* Reduced padding */}
          {assignments.length > 0 ? (
              assignments.map((assignment) => (
                  <ShiftCard
                      key={assignment.id}
                      assignment={assignment}
                      onRemove={() => onRemoveShift(department.id, assignment.id)}
                      isCompact // Use compact rendering for desktop week view cells
                  />
              ))
          ) : (
              <p className="text-[8px] lg:text-[9px] text-muted-foreground text-center py-0.5 italic">Vacío</p> // Smaller text
          )}
           {/* Optional: Add '+' button directly in the column for desktop week view? */}
           {/* <Button
                variant="ghost" size="xs" // Custom smaller size if needed
                className="w-full h-5 mt-1 text-muted-foreground hover:text-primary"
                onClick={() => onSelectEmployeeForShift(department.id, date)}
            >
                <Plus className="h-3 w-3" />
            </Button> */}
      </div>
    );
  }

  // Full rendering for day view (Desktop only, as mobile uses the week view structure)
  return (
    <Card ref={setNodeRef} style={style} className="flex flex-col h-full shadow-sm">
      <CardHeader className="flex flex-row items-center justify-between pb-2 pt-3 px-3 border-b"> {/* Reduced padding */}
         <CardTitle className="text-sm font-medium flex items-center gap-1 text-foreground"> {/* Adjusted size */}
             {department.icon && <department.icon className="h-3 w-3 text-muted-foreground" />}
             {department.name} ({assignments.length})
         </CardTitle>
         {/* Day View Add Button - Triggers employee selection */}
        <Button
            variant="ghost" size="icon" className="h-6 w-6 text-primary"
            onClick={() => onSelectEmployeeForShift(department.id, date)}
            title={`Añadir a ${department.name}`}
        >
          <Plus className="h-4 w-4" />
        </Button>
      </CardHeader>
      <CardContent className="flex-grow p-2 space-y-1 overflow-y-auto"> {/* Adjusted padding and space */}
        {assignments.length > 0 ? (
          assignments.map((assignment) => (
             <ShiftCard
                 key={assignment.id}
                 assignment={assignment}
                 onRemove={() => onRemoveShift(department.id, assignment.id)}
                 isCompact={false} // Not compact in day view details
             />
          ))
        ) : (
          <p className="text-xs text-muted-foreground text-center pt-4 italic">
            Arrastra o añade colaborador
          </p>
        )}
      </CardContent>
    </Card>
  );
};