import React from 'react';
import type { Department, ScheduleData, Employee } from '@/types/schedule'; // Added Employee type
import { DepartmentColumn } from './DepartmentColumn';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'; // Removed CardFooter
import { format, isSameDay } from 'date-fns'; // Added isSameDay
import { es } from 'date-fns/locale';
import { Button } from '../ui/button';
import { Copy, Eraser, Plus } from 'lucide-react'; // Added Plus icon
import { cn } from '@/lib/utils';
import { ShiftCard } from './ShiftCard'; // Import ShiftCard for mobile view

interface ScheduleViewProps {
  departments: Department[];
  scheduleData: { [dateKey: string]: ScheduleData };
  onRemoveShift: (dateKey: string, departmentId: string, assignmentId: string) => void;
  viewMode: 'day' | 'week';
  weekDates: Date[];
  currentDate: Date; // Also used as targetDate for day view and centering in mobile week view
  onAssign: (employee: Employee, departmentId: string, date: Date) => void; // Kept for day view D&D, mobile '+' needs adaptation
  getScheduleForDate: (date: Date) => ScheduleData;
  onDuplicateDay: (sourceDate: Date) => void;
  onClearDay: (dateToClear: Date) => void;
  isHoliday: (date: Date | null | undefined) => boolean;
  // Add handlers needed for mobile '+' button interaction
  onSelectEmployeeForShift: (departmentId: string, date: Date) => void; // Handler to open employee selection for mobile '+'
}

export const ScheduleView: React.FC<ScheduleViewProps> = ({
    departments,
    scheduleData,
    onRemoveShift,
    viewMode,
    weekDates,
    currentDate, // This is the targetDate
    onAssign,
    getScheduleForDate,
    onDuplicateDay,
    onClearDay,
    isHoliday,
    onSelectEmployeeForShift, // Receive the new handler
}) => {

    if (viewMode === 'day') {
         // --- Day View (Remains largely unchanged) ---
        const daySchedule = getScheduleForDate(currentDate);
        const dynamicGridClass = `grid-cols-1 sm:grid-cols-2 lg:grid-cols-${Math.min(departments.length, 4)} xl:grid-cols-${Math.min(departments.length, 5)}`;

        return (
            <Card className="shadow-md bg-card border border-border">
                <CardHeader className="border-b">
                    <CardTitle className="text-lg font-medium text-foreground">
                        Horario para el {format(currentDate, 'EEEE, d MMMM yyyy', { locale: es })}
                        {isHoliday(currentDate) && <span className="text-xs text-destructive font-semibold ml-2">(Festivo)</span>}
                    </CardTitle>
                </CardHeader>
                <CardContent className="p-4">
                    {departments.length > 0 ? (
                        <div className={`grid ${dynamicGridClass} gap-4`}>
                            {departments.map((department) => (
                                <DepartmentColumn
                                    key={department.id}
                                    department={department}
                                    assignments={daySchedule.assignments[department.id] || []}
                                    onRemoveShift={(deptId, assignId) => onRemoveShift(format(currentDate, 'yyyy-MM-dd'), deptId, assignId)}
                                    date={currentDate}
                                    onAssign={onAssign} // Used by Drag & Drop in Day view
                                    isWeekView={false} // Explicitly false for day view
                                    onSelectEmployeeForShift={onSelectEmployeeForShift} // Pass handler
                                />
                            ))}
                        </div>
                    ) : (
                         <p className="text-center text-muted-foreground italic py-4">
                             No hay departamentos definidos para esta sede. Agrega departamentos en la sección de configuración.
                         </p>
                    )}
                </CardContent>
            </Card>
        );
    } else {
        // --- Week View ---

        // --- Desktop Grid Layout (hidden on smaller screens) ---
        const weekViewDesktop = (
            <div className="hidden md:grid grid-cols-7 gap-1 w-full"> {/* Use gap-1 for tighter columns */}
                {weekDates.map((date, index) => {
                    const daySchedule = getScheduleForDate(date);
                    const dateKey = format(date, 'yyyy-MM-dd');
                    const totalAssignmentsForDay = Object.values(daySchedule.assignments).reduce((sum, deptAssignments) => sum + deptAssignments.length, 0);
                    const isLastDayOfWeek = index === weekDates.length - 1;
                    const isCurrentHoliday = isHoliday(date);

                    return (
                        <Card key={dateKey} className={cn(
                            "shadow-sm bg-card border flex flex-col min-w-[100px] lg:min-w-[120px]", // Reduced min-width
                            isCurrentHoliday ? "border-destructive border-2" : "border-border/50"
                        )}>
                            <CardHeader className={cn(
                                "pb-2 pt-2 px-2 border-b relative", // Reduced padding
                                isCurrentHoliday ? "border-destructive" : "border-border/50"
                            )}>
                                <CardTitle className={cn(
                                    "text-[10px] lg:text-xs font-medium text-foreground text-center whitespace-nowrap", // Smaller font size
                                    isCurrentHoliday && "text-destructive"
                                )}>
                                    {format(date, 'EEE d', { locale: es })}
                                </CardTitle>
                                <CardDescription className="text-[9px] lg:text-[10px] text-muted-foreground text-center"> {/* Smaller font size */}
                                    {format(date, 'MMM', { locale: es })} ({totalAssignmentsForDay})
                                    {isCurrentHoliday && <span className="text-destructive block text-[8px] lg:text-[9px]">Festivo</span>}
                                </CardDescription>
                                <div className="absolute top-0.5 right-0.5 flex flex-col gap-0"> {/* Adjusted position & gap */}
                                    {!isLastDayOfWeek && (
                                        <Button
                                            variant="ghost" size="icon"
                                            className="h-4 w-4 p-0 text-muted-foreground hover:text-foreground opacity-50 hover:opacity-100"
                                            onClick={() => onDuplicateDay(date)} title="Duplicar al día siguiente">
                                            <Copy className="h-2.5 w-2.5" /> {/* Smaller icon */}
                                        </Button>
                                    )}
                                    <Button
                                        variant="ghost" size="icon"
                                        className="h-4 w-4 p-0 text-destructive hover:text-destructive opacity-50 hover:opacity-100"
                                        onClick={() => onClearDay(date)} title="Limpiar turnos del día"
                                        disabled={totalAssignmentsForDay === 0}>
                                        <Eraser className="h-2.5 w-2.5" /> {/* Smaller icon */}
                                    </Button>
                                </div>
                            </CardHeader>
                            <CardContent className="p-1 space-y-1 flex-grow overflow-y-auto"> {/* Reduced padding and space */}
                                {departments.length > 0 ? (
                                    departments.map((department) => (
                                        <div key={department.id} className="border rounded p-1 bg-muted/10"> {/* Reduced padding */}
                                            <div className="flex justify-between items-center mb-0.5">
                                                <h4 className="text-[9px] lg:text-[10px] font-semibold text-foreground flex items-center gap-0.5 whitespace-nowrap overflow-hidden text-ellipsis"> {/* Smaller font, gap */}
                                                    {department.icon && <department.icon className="h-2 w-2 lg:h-2.5 lg:w-2.5 text-muted-foreground" />} {/* Smaller icon */}
                                                    <span className="overflow-hidden text-ellipsis">{department.name}</span>
                                                </h4>
                                                {/* Add '+' button for Desktop as well? Optional. */}
                                            </div>
                                            <DepartmentColumn
                                                department={department}
                                                assignments={daySchedule.assignments[department.id] || []}
                                                onRemoveShift={(deptId, assignId) => onRemoveShift(dateKey, deptId, assignId)}
                                                isWeekView
                                                date={date}
                                                onAssign={onAssign} // Still needed for potential future desktop '+'
                                                onSelectEmployeeForShift={onSelectEmployeeForShift} // Pass handler
                                            />
                                        </div>
                                    ))
                                ) : (
                                    <p className="text-center text-[10px] text-muted-foreground italic pt-1"> {/* Smaller text */}
                                        No hay deptos.
                                    </p>
                                )}
                            </CardContent>
                        </Card>
                    );
                })}
            </div>
        );

        // --- Mobile/Tablet Horizontal Scrolling Layout ---
        const weekViewMobile = (
            <div className="md:hidden flex overflow-x-auto space-x-3 py-4 px-2 snap-x snap-mandatory scroll-smooth">
                {weekDates.map((date, index) => {
                    const daySchedule = getScheduleForDate(date);
                    const dateKey = format(date, 'yyyy-MM-dd');
                    const isCurrent = isSameDay(date, currentDate); // Check if it's the target date
                    const isCurrentHoliday = isHoliday(date);

                    return (
                        <div
                            key={dateKey}
                            id={`day-card-${dateKey}`} // Add ID for potential scrolling
                            className={cn(
                                "w-64 sm:w-72 flex-shrink-0 snap-center bg-card rounded-lg border shadow-sm p-3 flex flex-col transition-transform duration-300",
                                isCurrent ? "border-primary ring-2 ring-primary ring-offset-2 scale-105 z-10" : "scale-95 opacity-80", // Highlight current day
                                isCurrentHoliday && !isCurrent && "border-destructive" // Add holiday border if not selected
                            )}
                        >
                            {/* Header */}
                            <div className={cn(
                                "text-center border-b pb-2 mb-2",
                                isCurrentHoliday && "border-destructive"
                            )}>
                                <p className={cn(
                                    "font-semibold text-base",
                                    isCurrentHoliday && "text-destructive"
                                )}>{format(date, 'EEE d', { locale: es })}</p>
                                <p className="text-xs text-muted-foreground">{format(date, 'MMMM yyyy', { locale: es })}</p>
                                 {isCurrentHoliday && <p className="text-xs text-destructive font-bold">(Festivo)</p>}
                            </div>

                            {/* Departments and Shifts */}
                            <div className="space-y-3 overflow-y-auto flex-grow">
                                {departments.length > 0 ? departments.map(department => (
                                    <div key={department.id} className="bg-muted/10 border rounded p-2">
                                        <div className="flex justify-between items-center mb-1">
                                            <h4 className="text-sm font-medium text-foreground flex items-center gap-1">
                                                {department.icon && <department.icon className="h-3 w-3 text-muted-foreground" />}
                                                {department.name}
                                            </h4>
                                            {/* Mobile Add Button */}
                                            <Button
                                                variant="ghost"
                                                size="icon"
                                                className="h-6 w-6 text-primary"
                                                onClick={() => onSelectEmployeeForShift(department.id, date)} // Trigger employee selection
                                                title={`Añadir a ${department.name}`}
                                            >
                                                <Plus className="h-4 w-4" />
                                            </Button>
                                        </div>
                                        <div className="space-y-1">
                                            {(daySchedule.assignments[department.id] || []).map(assignment => (
                                                <ShiftCard
                                                    key={assignment.id}
                                                    assignment={assignment}
                                                    onRemove={() => onRemoveShift(dateKey, department.id, assignment.id)}
                                                    isCompact={false} // Use slightly less compact view on mobile cards
                                                />
                                            ))}
                                            {(daySchedule.assignments[department.id] || []).length === 0 && (
                                                <p className="text-xs text-muted-foreground italic text-center py-1">Vacío</p>
                                            )}
                                        </div>
                                    </div>
                                )) : (
                                    <p className="text-center text-sm text-muted-foreground italic pt-4">No hay departamentos.</p>
                                )}
                            </div>
                        </div>
                    );
                })}
            </div>
        );


        return (
            <>
                {weekViewDesktop}
                {weekViewMobile}
            </>
        );
    }
};